import React, {Component} from 'react';
import {connect} from "react-redux";
import "./HowItSection.scss";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import TabSection from "./TabSection";

class HowItSection extends Component {

    state = {tab: 0};

    componentDidMount() {
    }

    handleChange = (event, newValue) => {
        this.setState({'tab': newValue});
    };

    render() {

        const {tab} = this.state;

        return (
            <div id="HowItSection">

                <Tabs
                    orientation="vertical"
                    variant="scrollable"
                    value={tab}
                    onChange={this.handleChange}
                    className="tabs"
                >
                    <Tab label="How it Works"/>
                    <Tab label="Which to Choose" />
                    <Tab label="Item Three" />
                    <Tab label="Item Four" />
                </Tabs>

                {tab === 0 && <TabSection title="How It Works"/>}

                {tab === 1 && <TabSection title="Which to Choose"/>}

                {tab === 2 && <TabSection title="Item 3"/>}

                {/*{tab === 3 && <div>
                    Item four
                </div>}*/}

            </div>
        );
    }
}

function mapStateToProps({}) {
    return {};
}

export default connect(mapStateToProps, {})(HowItSection);
