import React, {Component} from 'react';
import {connect} from "react-redux";
import "./Toolbar.scss"
import Button from "@material-ui/core/es/Button/Button";
import {APP_NAME} from "../../constants/constants";

class Toolbar extends Component {

    state = {showToolbarBackground: false};

    componentWillMount() {

        window.addEventListener('scroll', (e) => {

            if (window.scrollY > 100) {
                this.setState({showToolbarBackground: true})
            } else {
                this.setState({showToolbarBackground: false})
            }

        });

    }

    render() {

        const {showToolbarBackground} = this.state;
        const {toggleLoginOptions} = this.props;


        return (
            <div id="ToolbarCmpnt">

                <div className={"background transition " + (showToolbarBackground ? "showBackground" : "")}/>

                <div className={"flex-row"} id="toolbarContainer">

                   {/* <img className="logo" src={GenieSVG} alt="Genie"/>*/}

                    <div className="appName">
                        {APP_NAME}
                    </div>

                    <div className="flex-1"/>

                    <Button variant="outlined" className="border-color-white margin-right-16 toolbarButton">
                        <span className="toolbarButtonText" onClick={()=>{}}>Login</span>
                    </Button>


                </div>

            </div>
        );
    }
}

function mapStateToProps() {
    return {};
}

export default connect(mapStateToProps, {})(Toolbar);
